import EmptyMessage from "../../../../Utils/EmptyMessage/EmptyMessage";
import BorderBottom from "../../../../Ui/Border/BorderBottom";
import style from "../../../FinanceManager/FinanceManager.module.css";
import ManualExamSiteCard from "./ManualExamSiteCard";
import { useTranslation } from "react-i18next";
import CreateButton from "../../../../Ui/Button/CreateButton";
import { useState } from "react";
import AddManualExamSite from "./AddManualExamSite";
import { useExaminationDasboardSite } from "../../../../Hooks/ApiHooks/SiteManager/site-manager-api";
import Loading from "../../../../Loader/Loading";
import UserMemberContentWrapper from "../../../../Ui/UserMember/UserMemberContentWrapper";
import UserMemberContainerWrapper from "../../../../Ui/UserMember/UserMemberContainerWrapper";
const ManualExamSite = ({ lcid }) => {
  const { t } = useTranslation();
  const [add, setAdd] = useState(false);

  const {
    examinationDasboardSite,
    examinationDasboardSiteLoading,
    examinationDasboardSiteRefetch,
  } = useExaminationDasboardSite({
    data: {
      lcid: lcid,
    },
    skip: !lcid,
  });

  const onToggle = () => {
    setAdd((pre) => !pre);
  };
  return (
    <>
      <UserMemberContainerWrapper>
        <UserMemberContentWrapper>
          <div className={style.fm_header_container}>
            <h6>{t("manual_exam")}</h6>
            <CreateButton
              label="add"
              customStyle={{
                paddingBlock: "0.4rem",
                margin: "0",
              }}
              onAction={onToggle}
            />
          </div>
        </UserMemberContentWrapper>

        <BorderBottom />
        <UserMemberContentWrapper>
          {examinationDasboardSite?.manual_examination?.map((ssrReport) => (
            <ManualExamSiteCard
              ssrReport={ssrReport}
              key={ssrReport?._id}
              lcid={lcid}
              onRefetch={examinationDasboardSiteRefetch}
            />
          ))}
          {examinationDasboardSiteLoading ? (
            <Loading />
          ) : (
            examinationDasboardSite?.manual_examination?.length === 0 && (
              <EmptyMessage title="No manual exams." />
            )
          )}{" "}
        </UserMemberContentWrapper>
      </UserMemberContainerWrapper>

      {add && (
        <AddManualExamSite
          onClose={onToggle}
          lcid={lcid}
          onRefetch={examinationDasboardSiteRefetch}
        />
      )}
    </>
  );
};

export default ManualExamSite;
