import { useTranslation } from "react-i18next";
import ModalMenu from "../../../../Ui/Modal/ModalMenu";
import { useState } from "react";
import { useExaminationExtraDataRemove } from "../../../../Hooks/ApiHooks/SiteManager/site-manager-api";

const ManualExamSiteCardMenu = ({ lcid, onClose, menuData, onRefetch }) => {
  const { t } = useTranslation();
  const [disabled, setDisabled] = useState(false);

  const [examinationExtraDataRemove] = useExaminationExtraDataRemove();

  const onDelete = () => {
    if (menuData?._id && lcid) {
      setDisabled((pre) => !pre);
      examinationExtraDataRemove({
        lcid: lcid,
        bodyParameter: {
          cid: menuData?._id,
          flow: "MANUAL_EXAM",
        },
      })
        .then(() => {
          onRefetch();
          setDisabled((pre) => !pre);
          onClose();
        })
        .catch({});
    }
  };

  return (
    <>
      <ModalMenu onClose={onClose} disabled={disabled}>
        <h6 onClick={onDelete}>{t("delete")}</h6>
      </ModalMenu>
    </>
  );
};

export default ManualExamSiteCardMenu;
