import style from "../../../StoreManager/StoreManager.module.css";
import { useTranslation } from "react-i18next";
import FormFileView from "../../../../Ui/Input/FormFileView";
import { useState } from "react";
import ExamTimetableSiteCardMenu from "./ExamTimetableSiteCardMenu";
import { assestsUserFinanceUrl } from "../../../../Services/UrlConfig/AssestsBaseUrl";
const ExamTimetableSiteCard = ({ ssrReport, lcid, onRefetch }) => {
  const { t } = useTranslation();
  const [open, setOpen] = useState(false);
  const onToggle = () => {
    setOpen((pre) => !pre);
  };
  return (
    <>
      <div className={style.smc_card}>
        <div className={style.smc_card_contatiner}>
          <div className={style.smc_card_contatiner}>
            <div className={style.smc_card_contatiner_inner}>
              <h6>
                {" "}
                {t("name")} : {ssrReport?.name}
              </h6>
              <FormFileView
                label={`${t("attachment")} :`}
                value={{
                  key: ssrReport?.attach,
                }}
                customFormInputContainer={{
                  marginBlock: "0",
                }}
              />
            </div>
          </div>
        </div>
        <img
          src={`${assestsUserFinanceUrl}/menu.svg`}
          alt="menu icon"
          onClick={onToggle}
        />
      </div>

      {open && (
        <ExamTimetableSiteCardMenu
          onClose={onToggle}
          lcid={lcid}
          menuData={ssrReport}
          onRefetch={onRefetch}
        />
      )}
    </>
  );
};

export default ExamTimetableSiteCard;
