import { Suspense } from "react";
import UserMemberMainWrapper from "../../../Layout/UserMemberTab/UserMemberMainWrapper";
import QvipleLoader from "../../../Loader/QvipleLoader";
import UserMemberTabsWrapper from "../../../Ui/UserMember/UserMemberTabsWrapper";
import ExaminationSectionSiteProfile from "./ExaminationSectionSiteProfile";
import { useLocation, useSearchParams } from "react-router-dom";
import ManualExamSite from "./ManualExamSite/ManualExamSite";
import ExamScheduleSite from "./ExamScheduleSite/ExamScheduleSite";
import ExamTimetableSite from "./ExamTimetableSite/ExamTimetableSite";
import ExamNotificationSite from "./ExamNotificationSite/ExamNotificationSite";
import HallTickectExamSite from "./HallTickectExamSite/HallTickectExamSite";

const ExaminationSectionSite = () => {
  const getQuery = useLocation();
  const [searchParams] = useSearchParams();
  return (
    <>
      <UserMemberMainWrapper>
        <Suspense fallback={<QvipleLoader />}>
          {!searchParams.get("n") ? (
            <UserMemberTabsWrapper>
              <ExaminationSectionSiteProfile />
            </UserMemberTabsWrapper>
          ) : searchParams.get("n") === "manual" ? (
            <ManualExamSite lcid={getQuery.state?.lcid} />
          ) : searchParams.get("n") === "schedule" ? (
            <ExamScheduleSite lcid={getQuery.state?.lcid} />
          ) : searchParams.get("n") === "timetable" ? (
            <ExamTimetableSite lcid={getQuery.state?.lcid} />
          ) : searchParams.get("n") === "notification" ? (
            <ExamNotificationSite lcid={getQuery.state?.lcid} />
          ) : searchParams.get("n") === "ticket" ? (
            <HallTickectExamSite lcid={getQuery.state?.lcid} />
          ) : null}
        </Suspense>
      </UserMemberMainWrapper>
    </>
  );
};

export default ExaminationSectionSite;
