import { memo } from "react";
import { useLocation, useParams } from "react-router-dom";
import SquareTabWrapper from "../../../Ui/Tab/Square/SquareTabWrapper";
import SquareTabCard from "../../../Ui/Tab/Square/SquareTabCard";
import { assestsStoreUrl } from "../../../Services/UrlConfig/AssestsBaseUrl";
import style from "../../FinanceManager/FinanceManager.module.css";
import BorderBottom from "../../../Ui/Border/BorderBottom";
import { useTranslation } from "react-i18next";

const ExaminationSectionSiteProfile = () => {
  const { t } = useTranslation();
  const params = useParams();
  const getQuery = useLocation();

  let measure = document.getElementById("7892bdsb")?.clientWidth;
  let height = "9rem";
  let width = "9rem";
  if (measure) {
    let disvible =
      measure > 870 ? (measure > 1100 ? 6 : 5) : measure > 700 ? 4 : 3;
    let mi = measure > 870 ? 1 : 2;
    width = `${Math.floor((measure - mi - 31 * disvible - 1) / disvible)}px`;
    height = width;
  }
  return (
    <>
      <div className={style.fm_header_container}>
        <h6>{t("examination_section_site_info")}</h6>
      </div>
      <BorderBottom />
      <SquareTabWrapper dynamicId="7892bdsb">
        <SquareTabCard
          label="manual_exam"
          iconUrl={`${assestsStoreUrl}/unit.svg`}
          url={`/q/${params.username}/member/staff/examination?type=site&n=manual`}
          state={getQuery.state}
          isAuthorized
          // isVisible={tabManage ?? {}}
          isVisible={{
            store_unit: true,
          }}
          authorizedKey="store_unit"
          customStyleContainer={{
            height: height,
            width: width,
          }}
        />

        <SquareTabCard
          label="exam_schedule"
          iconUrl={`${assestsStoreUrl}/individual.svg`}
          url={`/q/${params.username}/member/staff/examination?type=site&n=schedule`}
          state={getQuery.state}
          isAuthorized
          // isVisible={tabManage ?? {}}
          isVisible={{
            individual_staff: true,
          }}
          authorizedKey="individual_staff"
          customStyleContainer={{
            height: height,
            width: width,
          }}
        />

        <SquareTabCard
          label="exam_timetable"
          iconUrl={`${assestsStoreUrl}/stock.svg`}
          url={`/q/${params.username}/member/staff/examination?type=site&n=timetable`}
          state={getQuery.state}
          isAuthorized
          // isVisible={tabManage ?? {}}
          isVisible={{
            stock: true,
          }}
          authorizedKey="stock"
          customStyleContainer={{
            height: height,
            width: width,
          }}
        />
        <SquareTabCard
          label="exam_notification"
          iconUrl={`${assestsStoreUrl}/issue.svg`}
          url={`/q/${params.username}/member/staff/examination?type=site&n=notification`}
          state={getQuery.state}
          isAuthorized
          // isVisible={tabManage ?? {}}
          isVisible={{
            issue_register: true,
          }}
          authorizedKey="issue_register"
          customStyleContainer={{
            height: height,
            width: width,
          }}
        />
        <SquareTabCard
          label="hall_ticket"
          iconUrl={`${assestsStoreUrl}/issue.svg`}
          url={`/q/${params.username}/member/staff/examination?type=site&n=ticket`}
          state={getQuery.state}
          isAuthorized
          // isVisible={tabManage ?? {}}
          isVisible={{
            issue_register: true,
          }}
          authorizedKey="issue_register"
          customStyleContainer={{
            height: height,
            width: width,
          }}
        />
      </SquareTabWrapper>
    </>
  );
};

export default memo(ExaminationSectionSiteProfile);
